import React, { Component } from "react";
import { connect } from "react-redux";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label, Row } from "reactstrap";
import InputElement from "../../components/InputElement/InputElement";
import Loader from "../../components/Loader/Loader";
import "../../../node_modules/video-react/dist/video-react.css"; // import css
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer";
import * as actions from "../../store/actions";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import _ from "lodash";
class VideoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      videoFile: null,
      uploadedFile: null,
      renderURL: "",
      videoTitle: "",
      videoRequired: false,
      videoInvalid: false,
      isVideo: true,
      videoFileNameExists: false,
      videoSizeExceeds: false
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ loading: false });
    }, 2000);
  }
  handleUploadFile = event => {
    if (event.target.files.length !== 0) {
      let video = event.target.files[0];
      if (video.type !== "" && _.includes(video.type, "video/mp4")) {
        this.setState({
          videoFile: video,
          renderURL: URL.createObjectURL(video),
          videoTitle: video.name,
          videoRequired: false,
          videoInvalid: false,
          videoFileNameExists: false,
          videoSizeExceeds: false
        });
      } else {
        this.setState({
          videoFile: video,
          renderURL: "",
          videoTitle: "",
          videoRequired: false,
          videoInvalid: true,
          videoFileNameExists: false,
          videoSizeExceeds: false
        });
      }
    } else {
      this.setState({
        videoFile: null,
        renderURL: "",
        videoTitle: "",
        videoRequired: false,
        videoInvalid: false,
        videoFileNameExists: false,
        videoSizeExceeds: false
      });
    }
  };

  onSubmitMedia() {
    let videoFile = this.state.videoFile;
    let compRef = this;
    if (this.validVideo(videoFile)) {
      if (this.fileNameExists(videoFile)) {
        if (videoFile.size < 11010048) {
          let videoData = new FormData();
          videoData.append("video", videoFile);
          this.props.postVideoFile(videoData);
          this.setState({ loading: true });
          let message = "";
          setTimeout(() => {
            compRef.props.videoError
              ? (message = "Something went wrong !")
              : (message = "Video uploaded successfully");
            compRef.onReset();
            compRef.setState({ loading: false });
            Toaster.Toaster(message, compRef.props.videoError);
            setTimeout(() => {
              if (!compRef.props.videoError) {
                compRef.onReset();
                compRef.props.history.push("/mediaContent/videoContent");
              }
            }, 1000);
          }, 1000);
        } else {
          this.setState({ videoSizeExceeds: true });
        }
      } else {
        this.setState({ videoFileNameExists: true });
      }
    }
  }
  fileNameExists(video) {
    let videoName = video.name.split(".")[0];
    let videoExists = _.filter(this.props.videoFiles, function(videoFile) {
      return videoFile.VideoName === videoName;
    });
    if (videoExists.length !== 0) {
      this.setState({ videoFileNameExists: true });
      return false;
    } else {
      return true;
    }
  }
  validVideo(video) {
    if (video && video.type !== "" && _.includes(video.type, "video/mp4")) {
      this.setState({ isVideo: true });
      return true;
    } else {
      if (!video) {
        this.setState({ videoRequired: true });
      }
      if (
        video &&
        (video.type === "" || !_.includes(video.type, "video/mp4"))
      ) {
        this.setState({ videoInvalid: true });
      }
      return false;
    }
  }
  onReset() {
    this.setState({
      videoFile: null,
      uploadedFile: null,
      renderURL: "",
      videoTitle: "",
      videoInvalid: false,
      videoRequired: false,
      videoFileNameExists: false,
      videoSizeExceeds: false
    });
  }
  render() {
    const {
      videoTitle,
      videoFile,
      renderURL,
      videoRequired,
      videoInvalid
    } = this.state;
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Video Upload"
        navigation={true}
        navigationRoute="/mediaContent/videoContent"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="6">
              <FormGroup row>
                <Col xs="12" md="10">
                  <InputElement
                    type="file"
                    label="Video file"
                    name="Video file"
                    accept="video/*"
                    value={videoFile === null ? "" : null}
                    required={videoRequired}
                    //invalid={videoInvalid}
                    onChange={event => this.handleUploadFile(event)}
                  />
                  {videoInvalid ? (
                    <Row>
                      <div className="help-block-media">
                        *Invalid video file. Please select mp4 video file.
                      </div>
                    </Row>
                  ) : null}
                  <Row>
                    {this.state.videoFileNameExists ? (
                      <div className="help-block-media">
                        *Video with same name already exists. Please change the
                        name of the video.
                      </div>
                    ) : null}
                  </Row>
                  <Row>
                    {this.state.videoSizeExceeds ? (
                      <div className="help-block-media">
                        *Please select video of size less than 10.5 MB.
                      </div>
                    ) : null}
                  </Row>
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="2">
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmitMedia.bind(this)}
                  >
                    Upload
                  </Button>
                </Col>
                <Col md="1">
                  <Button
                    className="theme-reset-btn"
                    onClick={this.onReset.bind(this)}
                  >
                    Reset
                  </Button>
                </Col>
              </FormGroup>
            </Col>
            <Col xs="12" md="6">
              <FormGroup row>
                {renderURL ? (
                  <Col xs="12" md="10">
                    <Label> {videoTitle}</Label>
                    <VideoPlayer source={renderURL} />
                  </Col>
                ) : null}
              </FormGroup>
            </Col>
          </FormGroup>
        </div>
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    videoError: state.mediaReducer.videoError,
    videoFiles: state.mediaReducer.videoFiles
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    postVideoFile: videoFile => dispatch(actions.postVideoFile(videoFile))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(VideoForm);



