import React, { Component } from "react";
import { connect } from "react-redux";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import InputElement from "../../components/InputElement/InputElement";
import AudioPlayer from "../../components/AudioPlayer/AudioPlayer";
import Loader from "../../components/Loader/Loader";
import * as actions from "../../store/actions";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import _ from "lodash";
class AudioForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      audioFile: null,
      uploadedFile: null,
      renderURL: "",
      audioTitle: "",
      audioRequired: false,
      audioInvalid: false,
      audioFileNameExists: false,
      audioName: "",
      isAudio: true
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ loading: false });
    }, 2000);
  }
  handleUploadFile = event => {
    if (event.target.files.length !== 0) {
      let audio = event.target.files[0];
      if (audio.type !== "" && _.includes(audio.type, "audio")) {
        this.setState({
          audioFile: audio,
          renderURL: URL.createObjectURL(audio),
          audioTitle: audio.name,
          audioRequired: false,
          audioInvalid: false,
          audioFileNameExists: false
        });
      } else {
        this.setState({
          audioFile: audio,
          renderURL: "",
          audioTitle: "",
          audioInvalid: true,
          audioRequired: false,
          audioFileNameExists: false
        });
      }
    } else {
      this.setState({
        audioFile: null,
        renderURL: "",
        audioTitle: "",
        audioRequired: false,
        audioInvalid: false,
        audioFileNameExists: false
      });
    }
  };

  onSubmitMedia() {
    let audioFile = this.state.audioFile;
    let compRef = this;
    if (this.validAudioFile(audioFile)) {
      if (this.fileNameExists(audioFile)) {
        let audioData = new FormData();
        audioData.append("audio", audioFile);
        this.props.postAudioFile(audioData);
        this.setState({ loading: true });
        let message = "";
        setTimeout(() => {
          compRef.props.audioError
            ? (message = "Something went wrong !")
            : (message = "Audio uploaded successfully");
          compRef.onReset();
          compRef.setState({ loading: false });
          Toaster.Toaster(message, compRef.props.audioError);
          setTimeout(() => {
            if (!compRef.props.audioError) {
              compRef.onReset();
              compRef.props.history.push("/mediaContent/audioContent");
            }
          }, 1000);
        }, 1000);
      } else {
        this.setState({audioFileNameExists : true});
      }
    }
  }
  fileNameExists(audio) {
    let audioName = audio.name.split(".")[0];
    let audioExists = _.filter(this.props.audioFiles, function(audioFile) {
      return audioFile.FileName === audioName;
    });
    if (audioExists.length !== 0) {
      this.setState({audioFileNameExists : true});
      return false;
    } else {
      return true;
    }
  }
  validAudioFile(audio) {
    if (audio && audio.type !== "" && _.includes(audio.type, "audio")) {
      this.setState({ isAudio: true });
      return true;
    } else {
      if (!audio) {
        this.setState({ audioRequired: true });
      }
      if (audio && (audio.type === "" || !_.includes(audio.type, "audio"))) {
        this.setState({ audioInvalid: true });
      }
      return false;
    }
  }
  onReset() {
    this.setState({
      audioFile: null,
      uploadedFile: null,
      renderURL: "",
      audioTitle: "",
      audioRequired: false,
      audioInvalid: false,
      audioName: "",
      audioFileNameExists :false
    });
  }
  render() {
    const {
      audioTitle,
      audioFile,
      renderURL,
      audioRequired,
      audioInvalid
    } = this.state;
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Audio Upload"
        navigation={true}
        navigationRoute="/mediaContent/audioContent"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="6">
              <FormGroup row>
                <Col xs="12" md="10">
                  <InputElement
                    type="file"
                    label="Audio file"
                    name="Audio file"
                    accept="audio/*"
                    value={audioFile === null ? "" : null}
                    required={audioRequired}
                    invalid={audioInvalid}
                    onChange={event => this.handleUploadFile(event)}
                  />
                  {this.state.audioFileNameExists ? (
                  <div className="help-block-media">
                    *Audio file with same name already exists. Please change the
                    name of the file.
                  </div>
                ) : null}
                </Col>
              </FormGroup>
              
              <FormGroup row>
                <Col xs="12" md="2">
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmitMedia.bind(this)}
                  >
                    Upload
                  </Button>
                </Col>
                <Col md="1">
                  <Button
                    className="theme-reset-btn"
                    onClick={this.onReset.bind(this)}
                  >
                    Reset
                  </Button>
                </Col>
              </FormGroup>
            </Col>
            <Col xs="12" md="6">
              <FormGroup row>
                {renderURL ? (
                  <Col xs="12" md="10">
                    <Label> {audioTitle}</Label>
                    <AudioPlayer
                      source={renderURL}
                      autoPlay={false}
                      title={audioTitle}
                    />
                  </Col>
                ) : null}
              </FormGroup>
            </Col>
          </FormGroup>
        </div>
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    audioError: state.mediaReducer.audioError,
    audioFiles: state.mediaReducer.audioFiles
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    postAudioFile: audioFile => dispatch(actions.postAudioFile(audioFile))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(AudioForm);



